.galaxy-loader {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(16, 16, 16)), to(rgb(21, 20, 20)));
    background: -webkit-linear-gradient(rgb(16, 16, 16), rgb(21, 20, 20));
    background: -o-linear-gradient(rgb(16, 16, 16), rgb(21, 20, 20));
    background: linear-gradient(rgb(16, 16, 16), rgb(21, 20, 20));
}

.galaxy-loader__icon {
    width: 60px;
    height: 60px;
    fill: rgba(255, 255, 255, 0.9);
    -webkit-animation-name: galaxy-loader-anim;
            animation-name: galaxy-loader-anim;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    margin-bottom: 30px;
}

.galaxy-loader__text {
    font-size: 16px;
    color: white;
}

@-webkit-keyframes galaxy-loader-anim {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

@keyframes galaxy-loader-anim {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}


